<template>
  <li @click="$emit('playSong', [n.id, n]);$emit('play-list')" :class="{ active: songId === n.id, 'recom-li': true }">
    <!-- {{n.name}} -->
    <p>{{ ("" + (index + 1)).padStart(2, "0") }}</p>
    <div>
      <h2>
        {{ n.name }} <span v-if="n.alia.length">({{ yyy(n.alia) }})</span>
      </h2>
      <p><span v-if="n.sq ? true : false"></span>{{ xxx(n.ar) }} - {{ n.al.name }}</p>
    </div>
    <div :class="{ activeLoding: true, play: playStatus }" v-if="songId === n.id">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
    <span v-else></span>
  </li>
</template>

<script>
export default {
  props: {
    n: Object,
    index: Number,
    songId:Number,
    playStatus:Boolean
  },
  methods: {
    xxx(ar) {
      let a = "";
      if (ar.length > 1) {
        ar.forEach((e) => {
          a += e.name + " / ";
        });
        return a;
      } else {
        return (a = ar[0]?.name);
      }
    },
    yyy(ar) {
      let a = "";
      ar.forEach((e) => {
        a += e + " ";
      });
      return a;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes loding {
  from {
    height: 20rem;
  }
  to {
    height: 12rem;
  }
}
.recom-li {
  display: flex;
  justify-content: space-between;
  padding: 6rem 0;
  align-items: center;
  border-bottom: 0.1rem solid rgba(221, 221, 221, 0.656);
  &.active {
    > div {
      h2 {
        color: red;
      }
    }
    > div.activeLoding {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      width: 40rem;
      height: 40rem;
      flex-shrink: 0;
      padding: 10rem;
      > span {
        width: 2rem;
        height: 20rem;
        background-color: red;
        animation: loding 0.6s infinite ease-in-out alternate;
        animation-play-state: paused;
      }
      &.play {
        span {
          animation-play-state: running;
        }
      }
      > span:nth-of-type(1) {
        animation-delay: 0;
      }
      > span:nth-of-type(2) {
        animation-delay: -0.2s;
      }
      > span:nth-of-type(3) {
        animation-delay: -0.4s;
      }
      > span:nth-of-type(4) {
        animation-delay: -0.6s;
      }
    }
  }
  > div {
    width: 100%;
    font-size: 17rem;
    color: #333;
    h2 {
      height: 25.5rem;
      width: calc(375rem - 16rem - 22rem - 43.5rem);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      > span {
        color: #888;
      }
    }
    > p {
      font-size: 12rem;
      color: #888;
      height: 18rem;
      width: calc(375rem - 16rem - 22rem - 43.5rem);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      > span {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        margin-right: 4rem;
        background-image: url("../assets/index_icon_2x.png");
        background-size: 166rem 97rem;
        background-repeat: no-repeat;
      }
    }
  }
  > span {
    flex-shrink: 0;
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    margin: 0 10rem;
    background-image: url("../assets/index_icon_2x.png");
    background-size: 166rem 97rem;
    background-repeat: no-repeat;
  }
  > p {
    flex-shrink: 0;
    width: 40rem;
    height: 43.5rem;
    font-size: 17rem;
    text-align: center;
    line-height: 43.5rem;
  }
}
</style>
