      <p><span v-if="n.song.sqMusic ? true : false"></span>{{ n.song.album.artists[0].name }} - {{ n.song.album.name }}</p>
    </div>
    <div :class="{ activeLoding: true, play: playStatus }" v-if="songId === n.id">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
    <span v-else></span>
  </li>
</template>

