<template>
  <div class="playBar">
    <figure :class="{ playActive: playStatus }">
      <img :class="{ playActive: playStatus }" :src="`${song.picUrl ? song.picUrl : song.al.picUrl}?imageView=1&type=webp&thumbnail=70x0`" />
    </figure>
    <div class="content">
      <!-- <h4>{{ song.name }} - {{forName(song.song ? song.song.artists:song.ar)}}</h4> -->
      <h4>{{ song.name }} - {{ forName(song.song ? song.song.artists : song.ar) }}</h4>
    </div>

    <div class="playControl">
      <div>
        <button v-show="!playStatus" @click.stop="$emit('playStatus')" style="transform: translateX(2rem)"><i class="fa fa-play"></i></button>
        <button v-show="playStatus" @click.stop="$emit('pauseStatus')"><i class="fa fa-pause"></i></button>
      </div>
      <canvas ref="canvas" width="500" height="500"></canvas>
    </div>

    <div class="list"  @click.stop="$emit('play-list-show')">
      <i class="fa fa-list-ul"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playStatus: Boolean,
    songId: Number,
    currentTime: Number,
    duration: Number,
  },
  data: function () {
    return {
      isPlay: null,
    };
  },
  watch: {
    currentTime(n) {
      let ctx = this.$refs.canvas;
      let con = ctx.getContext("2d");
      con.clearRect(0, 0, 500, 500);
      con.beginPath();
      // con.strokeStyle = "#d43c33";
      con.strokeStyle = "#fff";
      con.arc(250, 250, 240, -0.5 * Math.PI, Math.PI * (2 * (n / this.duration) - 0.5));
      con.lineWidth = 20;
      con.stroke();
    },
  },
  methods: {
    forName(n) {
      // console.log(n);
      let a = "";
      n.forEach((e, i) => {
        a += e.name + (i === n.length - 1 ? "" : " /");
      });
      return a;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes playPoster {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes playPosterGaoLiang {
  from {
    transform: rotate(0) scale(1.2);
  }
  to {
    transform: rotate(360deg) scale(1.2);
  }
}

.playBar {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: rgb(62, 62, 62);
  height: 60rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 10rem;
  padding-right: 10rem;
  border-top: 1px solid rgba(205, 205, 205, 0.3);
  .content {
    width: 100%;
    flex-shrink: 1;
    color: aliceblue;
    overflow: hidden;
    h4 {
      padding: 0 10rem;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      width: 100%;
      font-size: 12rem;
    }
  }
  figure {
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translateY(-8rem);
    border: 2rem solid #00000087;
    flex-shrink: 0;
    position: relative;
    &::after {
      content: "";
      left: 0;
      top: 0;
      border-radius: 50%;
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: url("https://s3.music.126.net/mobile-new/img/disc_light.png?2bb24f3fd11569809b817b4988f12bc7=");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      animation: playPosterGaoLiang 8s linear infinite;
      animation-play-state: paused;
    }
    &.playActive {
      animation-play-state: running;
      &::after {
        animation-play-state: running;
      }
    }
    img {
      width: 60rem;
      height: 60rem;
      background-color: #1a191a;
      border-radius: 50%;
      animation: playPoster 8s linear infinite;
      animation-play-state: paused;
      border: 10rem solid #1a191a;
      &.playActive {
        animation-play-state: running;
      }
    }
  }
  .playControl {
    flex-shrink: 0;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    > canvas {
      width: 40rem;
      height: 40rem;
    }

    > div {
      top: 0;
      left: 0;
      position: absolute;
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      justify-content: center;
      border: 2rem solid rgba(255, 255, 255, 0.216);
      border-radius: 50%;
      // z-index: -1;
      box-sizing: border-box;
      button {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 18rem;
        background-color: transparent;
        border: none;
        outline: none;
        color: aliceblue;
      }
    }
  }
  .list {
    flex-shrink: 0;
    width: 50rem;
    height: 50rem;
    display: flex;
    justify-content: center;
    align-items: center;
    i {
      font-size: 20rem;
      color: aliceblue;
    }
  }
}
</style>
