import Vue from 'vue'
import VueRouter from 'vue-router'
// import HomeView from '../views/HomeView.vue'
import recomMusic from "../views/recomMusic.vue";
import hotMusic from "../views/hotMusic.vue";
import seekMusic from "../views/seekMusic.vue";
import songList from "../views/songListView.vue";
import HomeViewRouter from "../views/HomeViewRouter.vue";
import artistView from "../views/artistView.vue";
import playlist from "../views/PlaylistView.vue";
import AlbumView from "../views/AlbumView.vue";
import LyricView from "../views/LyricView.vue";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: HomeViewRouter,
    meta: { showSeekView: true },
    children: [
      {
        path: '/',
        component: recomMusic,
        meta: { showSeekView: true },

        // meta: { showViewHome: true }
      },
      {
        path: 'hotMusic',
        component: hotMusic,
        meta: { showSeekView: true },

        // meta: { showViewHome: true }
      },
      {
        path: 'seekMusic',
        component: seekMusic,
        meta: { showSeekView: true },
        // meta: { showViewHome: true }
      },
    ]
  },
  {
    path: '/songList',
    component: songList,
  },
  {
    path: '/artist',
    name: 'artist',
    component: artistView,
  },
  {
    path: '/playlist',
    component: playlist
  },
  {
    path: "/album",
    component: AlbumView
  },
  {
    path: '/lyric',
    component: LyricView,
    meta: { showSeekView: true }
  }
]

const router = new VueRouter({
  routes
})

export default router
