<template>
  <div class="hot-home">
    <div class="hot-top">
      <div></div>
      <p>更新日期：{{ new Date(hotTime).toLocaleDateString() }}</p>
    </div>
    <ul v-if="isShow&&isOK">
      <hotLi @play-list="$emit('play-list',hotMusic)" :songId="songId" :playStatus="playStatus" @playSong="$emit('playSong',$event)" v-for="(p, index) in hotMusic" :key="p.id" :n="p" :index="index"></hotLi>
    </ul>
    <div style="display: flex; justify-content: center" v-else-if="!isShow && !isOK">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div style="text-align: center" v-if="isShow && !isOK">加载失败！</div>
    <footer v-if="isShow&&isOK"><span>查看完整榜单</span></footer>
  </div>
</template> 

<script>
import hotLi from "../components/hotLi.vue";
export default {
  props:{
    songId:Number,
    playStatus:Boolean
  },
  data: () => ({
    hotTime: 0,
    hotMusic: [],
    isShow:false,
    isOK:false
  }),
  created() {
    // https://apis.netstart.cn/music/playlist/detail?id=3778678
    this.axios("https://apis.netstart.cn/music/playlist/detail?id=3778678").then((res) => {
      this.hotMusic = res.data.playlist.tracks.slice(0, 20);
      this.hotTime = res.data.playlist.trackUpdateTime;
      // console.log(res.data.playlist.tracks);
      // console.log(new Date(res.data.playlist.trackUpdateTime).toLocaleDateString());
      this.isOK = true
    }).catch(()=>{this.isOK=false}).finally(()=>{this.isShow=true})
    // new Date(1663602902059).getMonth()
    // new Date(1663602902059).getDate()
  },
  components: {
    hotLi,
  },
};
</script>

<style lang="less" scoped>
.hot-home {
  .hot-top {
    height: 146rem;
    background-image: url("../assets/hot_music_bg_2x.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    position: relative;
    z-index: 0;
    padding-left: 20rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    &::after {
      z-index: -1;
      content: "";
      background-color: rgba(0, 0, 0, 0.2);
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
    }
    > div {
      height: 67rem;
      width: 142rem;
      background-image: url("../assets/index_icon_2x.png");
      background-size: 166rem 97rem;
      background-position: -24rem -30rem;
    }
    > p {
      height: 18rem;
      margin-top: 10rem;
      font-size: 12rem;
      color: rgba(255, 255, 255, 0.8);
    }
  }
  > ul {
    > li:nth-of-type(-n + 3) {
      color: #d43c33;
    }
  }
  footer {
    width: 100%;
    color: #999;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14rem;
    span {
      display: inline-block;
      height: 55rem;
      line-height: 55rem;
      padding-right: 14rem;
      background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxNCAyMiI+PHBhdGggZmlsbD0ibm9uZSIgc3Ryb2tlPSIjY2NjIiBzdHJva2Utd2lkdGg9IjIiIHN0cm9rZS1taXRlcmxpbWl0PSIxMCIgZD0iTTEgMWwxMCAxMEwxIDIxIi8+PC9zdmc+");
      background-size: 7rem 14rem;
      background-repeat: no-repeat;
      background-position: 100%;
    }
  }
}
</style>
