<style lang="less" scoped>
@keyframes loding {
  from {
    height: 20rem;
  }
  to {
    height: 12rem;
  }
}
.recom-li {
  display: flex;
  justify-content: space-between;
  padding: 6rem 0;
  align-items: center;
  border-bottom: 0.1rem solid rgba(221, 221, 221, 0.656);
  &.active {
    > div {
      h2 {
