      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      width: 40rem;
      height: 40rem;
      flex-shrink: 0;
      padding: 10rem;
      > span {
        width: 2rem;
        height: 20rem;
        background-color: red;
        animation: loding 0.6s infinite ease-in-out alternate;
        animation-play-state: paused;
      }
      &.play {
        span {
          animation-play-state: running;
        }
      }
      > span:nth-of-type(1) {
        animation-delay: 0;
      }
      > span:nth-of-type(2) {
        animation-delay: -0.2s;
      }
