<template>
  <div class="hot-search">
    <div>
      <p>热门搜索</p>
      <ul>
        <li v-for="(hot, index) in hotSea" :key="index" @click="$emit('click-li', hot.first)">{{ hot.first }}</li>
      </ul>
    </div>
    <section class="past">
      <ul>
        <li v-for="(p, index) in past" :key="index">
          <i></i>
          <div>
            <p @click="$emit('click-li', p.pastName)">{{ p.pastName }}</p>
            <div @click="$emit('del-past', p.id)">
              <i></i>
            </div>
          </div>
        </li>
      </ul>
    </section>
  </div>
</template>

<script>
export default {
  created() {
    this.axios("https://apis.netstart.cn/music/search/hot").then((res) => {
      this.hotSea = res.data.result.hots;
    });
  },
  data: () => ({
    hotSea: [],
  }),
  props: {
    past: Array,
  },
};
</script>

<style lang="less" scoped>
.hot-search {
  > div:first-of-type {
    padding: 15rem 10rem 0;
    > p {
      color: #333;
      font-size: 12rem;
    }
    > ul {
      display: flex;
      flex-wrap: wrap;
      padding: 10rem 0 7rem 0;
      > li {
        color: #333;
        height: 32rem;
        line-height: 32rem;
        color: #333;
        font-size: 14rem;
        padding: 0 14rem;
        border: 1rem solid rgba(136, 136, 136, 0.2);
        border-radius: 100vw;
        margin: 0 8rem 8rem 0rem;
      }
    }
  }
  .past {
    > ul {
      > li {
        display: flex;
        line-height: 45rem;
        align-items: center;
        i {
          flex-shrink: 0;
          display: inline-block;
          width: 15rem;
          height: 15rem;
          margin: 0 10rem;
          background-image: url("data:image/svg+xml;base64,");
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
        }
        > div {
          width: 100%;
          height: 45rem;
          display: flex;
          border-bottom: 1rem solid rgba(221, 221, 221, 0.327);
          overflow: hidden;
          > p {
            padding-right:  15rem;
            width: 100%;  
            font-size: 14rem;
            color: #4b4b4b;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
          }
          > div {
            flex-shrink: 0;
            width: 32rem;
            height: 32rem;
            text-align: left;
            line-height: 45rem;
            > i {
              width: 12rem;
              height: 12rem;
              background-image: url("data:image/svg+xml;base64,");
              background-position: center;
              background-size: cover;
              background-repeat: no-repeat;
              margin: 0;
            }
          }
        }
      }
    }
  }
}
</style>
