<template>
  <div class="playlist" v-if="playlist?.coverImgUrl">
    <div class="playing_title">
      <i
        class="fa fa-reply"
        @click="
          // $router.push({ path: beforePath });
          $router.back()
        "
      ></i>
      <div class="playing_song">
        <!-- <p class="singername">{{ song?.ar[0].name }}</p> -->
      </div>
      <i class="fa fa-share-square-o"></i>
    </div>
    <!-- 背景图 -->
    <div class="bg" :style="playlist && 'backgroundImage:url(' + playlist?.coverImgUrl + ')'"></div>

    <!-- 顶部 -->
    <header>
      <!-- 封面 -->
      <div class="cover">
        <span class="earphone">{{ playcount + "万" }}</span>
        <i>歌单</i>
        <img :src="playlist?.coverImgUrl" width="226" alt="" />
      </div>
      <!-- 描述 -->
      <div class="description">
        <p>{{ playlist.name }}</p>
        <div class="author">
          <img :src="playlist?.creator?.avatarUrl" width="30" alt="" />
          <i class="type"></i>
          <span>{{ playlist?.creator?.nickname }}</span>
        </div>
      </div>
    </header>

    <!-- 详情列表 -->
    <!-- 单曲 -->
    <SongsDetails :playStatus="playStatus" :songId="songId" :singlesongs="singlesongs" @playSong="$emit('playSong', $event)" @play-list="$emit('play-list', $event)">
    </SongsDetails>
    <!-- 评论 -->
    <CommentsDetails :hotcomments="hotcomments" :newcomments="newcomments" :total="total"> </CommentsDetails>
  </div>
</template>

<script>
import SongsDetails from "@/components/SongsDetails.vue";
import CommentsDetails from "@/components/CommentsDetails.vue";
export default {
  props: {
    playStatus: Boolean,
    songId: Number,
  },
  components: {
    SongsDetails,
    CommentsDetails,
  },
  data: function () {
    return {
      playlist: [],
      singlesongs: [],
      hotcomments: [],
      newcomments: [],
      total: null,
    };
  },
  computed: {
    playcount: function () {
      return (this.playlist.playCount / 10000).toFixed(1).split(".")[1] == 0 ? (this.playlist.playCount / 10000).toFixed(0) : (this.playlist.playCount / 10000).toFixed(1);
    },
  },
  methods: {
    getPlayList: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/playlist/detail", {
          params: { id },
        })
        .then((res) => {
          this.playlist = res.data.playlist;
          this.singlesongs = res.data.playlist.tracks;
        });
    },
    getComments: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/playlist", {
          params: { id, limit: 10 },
        })
        .then((res) => {
          this.hotcomments = res.data.hotComments;
          this.newcomments = res.data.comments;
          this.total = res.data.total;
        });
    },
  },

  created: function () {
    this.getPlayList(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.playing_title {
  width: 100%;
  height: 50rem;
  padding: 0 30rem;
  position: fixed;
  top: 0;
  z-index: 9999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.255);
  .playing_song {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    .songname {
      font-size: 16rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      text-align: center;
    }
    .singername {
      margin-top: 5rem;
      color: #fff;
    }
  }
}
.playlist {
  overflow: hidden;
  .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: -1;
    filter: blur(20rem);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center -200rem;
  }
  header {
    padding: 50rem 15rem 30rem;
    display: flex;
    .cover {
      position: relative;
      i {
        display: block;
        position: absolute;
        top: 12rem;
        left: 0;
        background: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 10rem;
        border-bottom-right-radius: 10rem;
        color: white;
        font-size: 12rem;
        padding: 1rem 10rem;
      }
      span.earphone {
        position: absolute;
        right: 0;
        top: 0;
        display: block;
        padding: 3rem 5rem 0 14rem;
        color: white;
        background-image: url("data:image/svg+xml;base64,");
        background-repeat: no-repeat;
        background-size: 12rem;
        background-position: left 6rem;
      }
    }
    .description {
      margin-left: 10rem;
      font-size: 17rem;
      padding-top: 5rem;
      color: white;
      .author {
        margin-top: 3rem;
        display: flex;
        align-items: center;
        font-size: 12rem;
        position: relative;
        img {
          border-radius: 50rem;
          margin-right: 5rem;
        }
        i.type {
          display: block;
          position: absolute;
          bottom: 0;
          left: 23rem;
          width: 12rem;
          height: 12rem;
          background-image: url("../assets/usericn_2x.png");
          background-position: -40rem 0;
          background-size: 75px 25px;
          background-repeat: no-repeat;
        }
        span {
          color: hsla(0, 0%, 100%, 0.7);
        }
      }
    }
  }
}
</style>
