<template>
  <div class="seek-home">
    <div class="seek-top">
      <input
        type="text"
        v-model="value"
        @keyup.enter="
        value && (setAdd(),isResult = true,$event.target.blur())
        "
        @focus="
          isResult = false;
          focusUpdata(value);
        "
        placeholder="搜索歌曲、歌手、专辑"
      />
      <span v-show="value" @click="value = ''"></span>
    </div>

    <SeekLi
      v-show="isSeekLi && !isResult"
      :value="value"
      :isResult="isResult"
      :focusValue="focusValue"
      @click-li="
        value = $event;
        isResult = true;
        setAdd();
      "
      @focus-updata="focusValue = ''"
    ></SeekLi>
    <hotSearch
      v-if="!isSeekLi && !isResult"
      :past="past"
      @del-past="delPast"
      @click-li="
        value = $event;
        isResult = true;
        setAdd();
      "
    ></hotSearch>
    <seekResult
      @play-list="$emit('play-list', $event)"
      :playStatus="playStatus"
      :songId="songId"
      @playSong="$emit('playSong', $event)"
      v-if="isResult"
      :seekValue="value"
      ref="seekScrollTop"
    ></seekResult>
  </div>
</template>

<script>
import hotSearch from "../components/hotSearch.vue";
import SeekLi from "../components/seekLi.vue";
import seekResult from "../components/seekResult.vue";
export default {
  props: {
    playStatus: Boolean,
    songId: Number,
  },
  components: {
    hotSearch,
    SeekLi,
    seekResult,
  },
  data: () => ({
    past: JSON.parse(localStorage.getItem("music-past")) || [],
    value: "",
    isResult: false,
    focusValue: "",
  }),
  beforeRouteEnter(to, from, next) {
    // console.log(to);
    // console.log(from);

    next((vm) => {
      if (sessionStorage.getItem("music-past-value")) {
        vm.value = sessionStorage.getItem("music-past-value");
        vm.setAdd();
        vm.isResult = true;
      }
    });
  },
  beforeRouteLeave(to, from, next) {
    if (this.value && this.isResult) {
      sessionStorage.setItem("music-past-value", this.value);
    } else {
      sessionStorage.setItem("music-past-value", "");
    }
    next();
  },
  computed: {
    //是否显示
    isSeekLi() {
      if (this.value == "") {
        this.isreult();
      }
      return this.value != "";
    },
  },
  methods: {
    // 历史记录
    setAdd() {
      this.past = this.past.filter((e) => e.pastName != this.value);
      this.past.unshift({
        id: !this.past.length ? 1 : Math.max(...this.past.map((e) => e.id)) + 1,
        pastName: this.value,
      });
      localStorage.setItem("music-past", JSON.stringify(this.past));
    },
    // 历史记录
    delPast(id) {
      this.past = this.past.filter((e) => e.id != id);
      localStorage.setItem("music-past", JSON.stringify(this.past));
    },
    // 显示搜索结果
    isreult() {
      this.isResult = false;
    },
    focusUpdata(value) {
      this.focusValue = value;
    },
  },
};
</script>

<style lang="less" scoped>
.seek-home {
  .seek-top {
    width: 100%;
    padding: 15rem 10rem;
    display: flex;
    align-items: center;
    box-shadow: 0 0 2rem 0 #c9c9c9;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: calc(10rem + 8.5rem);
      top: calc(8.5rem+15rem);
      width: 13rem;
      height: 13rem;
      z-index: 1;
      background-image: url("data:image/svg+xml;base64,");
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
    }
    input {
      width: 100%;
      height: 30rem;
      border-radius: 100vw;
      text-indent: 29rem;
      background-color: #ebecec;
      border: none;
      outline: none;
      font-size: 12rem;
      &::placeholder {
        color: #c9c9c9;
      }
    }
    > span {
      position: absolute;
      right: 24rem;
      font-size: 12rem;
      width: 14rem;
      height: 14rem;
      background-image: url("data:image/svg+xml;base64,");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
    }
  }
}
</style>
