<template>
  <div class="artist">
    <header class="playing_title">
      <i
        class="fa fa-reply"
        @click="
          // $router.push({ path: beforePath });
          $router.back()
        "
      ></i>
      <div class="playing_song">
      </div>
      <i class="fa fa-share-square-o"></i>
    </header>

    <section class="hd">
      <img :src="artistArr?.artist?.cover" />
      <div class="content">
        <h2>{{ artistArr?.artist?.name }}</h2>
        <h4 v-if="artistArr?.artist?.rank?.rank">歌手榜华语地区 No.{{ artistArr?.artist?.rank?.rank }}</h4>
        <div class="nick">
          <p v-if="artistArr?.user?.nickname">昵称: {{ artistArr?.user?.nickname }}</p>
          <p class="fs">
            <span v-if="fans?.followCnt || fans?.followCnt === 0">关注 {{ fans?.followCnt }}&nbsp;&nbsp; |</span> &nbsp;&nbsp;粉丝
            {{
              fans?.fansCnt >= 10000 ? (fans?.fansCnt >= 100000000 ? (fans?.fansCnt / 100000000).toFixed(1) + "亿" : "" + (fans?.fansCnt / 10000).toFixed(1) + "万") : fans?.fansCnt
            }}
          </p>
          <p>
            <span v-if="artistArr?.artist?.identifyTag != null">{{ artistArr?.artist?.identifyTag[0] }} </span>
            <span class="skill" v-for="(i, index) in artistArr?.artist?.identities" :key="index">{{ i }}</span>
          </p>
        </div>
      </div>
    </section>
    <h2>歌手简介</h2>
    <div class="intro">
      <p :class="introClass">{{ artistArr?.artist?.briefDesc }}</p>
      <div v-show="introClass == 'shou'" @click="introClass = 'zhang'" class="full">完整歌手介绍<i class="arrow"></i></div>
      <div v-show="introClass == 'zhang'" @click="introClass = 'shou'" class="full">收起<i class="arrow"></i></div>
    </div>

    <h2 v-if="hotSong.length">热门单曲</h2>
    <ul v-if="hotSong.length">
      <hotLi
        @play-list="$emit('play-list', hotSong)"
        @playSong="$emit('playSong', $event)"
        :playStatus="playStatus"
        :songId="songId"
        v-for="(h, i) in hotSong"
        :key="h.id"
        :n="h"
        :index="i"
      ></hotLi>
    </ul>

    <h2 v-if="albumObj?.album?.blurPicUrl">最新音乐</h2>
    <div v-if="albumObj?.album?.blurPicUrl" class="albumClass" @click="$router.push({ path: '/album', query: { id: albumObj?.album?.id } })">
      <figure>
        <img :src="albumObj.album?.blurPicUrl" alt="" />
      </figure>
      <div class="content">
        <h4>{{ albumObj?.album?.name }}</h4>
        <p>{{ albumObj?.album?.artist?.name }}</p>
        <p>
          <span>{{ albumObj?.songs?.length }}首 </span> {{ new Date(albumObj?.album?.publishTime).toLocaleDateString() }}
        </p>
      </div>
    </div>
    <!-- <h2 v-if="true" class="h2Show">演出信息 <span>(共?场)</span></h2>
    <div v-if="true" class="more-concert">更多演出<i class="arrow arrow-right"></i></div> -->

    <div class="artistFooter">
      <a href="javascript:void(0)">下载热门歌曲</a>
    </div>
    <div style="height: 62rem"></div>
  </div>
</template>

<script>
import hotLi from "../components/artistHotSong.vue";
import get from "../axiosApi/getArtist";
export default {
  props: {
    playStatus: Boolean,
    songId: Number,
  },
  components: {
    hotLi,
  },
  data: () => ({
    artistArr: [],
    fans: {},
    introClass: "shou",
    hotSong: [],
    albumObj: {},
  }),
  created() {
    get(this.$route.query.id).then((res) => {
      //   console.log(res);
      this.artistArr = res.data.data;
    });
    this.axios("https://apis.netstart.cn/music/artist/follow/count", { params: { id: this.$route.query.id } }).then((res) => {
      this.fans = res.data.data;
    });
    this.axios("https://apis.netstart.cn/music/artist/top/song", { params: { id: this.$route.query.id } }).then((res) => {
      this.hotSong = res.data.songs.slice(0, 5);
    });
    if (this.$route.query.albumId) {
      this.axios
        .get("https://apis.netstart.cn/music/album", {
          params: { id: this.$route.query.albumId },
        })
        .then((res) => {
          this.albumObj = res.data;
        });
    }
  },
};
</script>

<style lang="less" scoped>
.playing_title {
  width: 100%;
  height: 40rem;
  padding: 0 30rem;
  position: fixed;
  top: 0;
  z-index: 9999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.115);
  .playing_song {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    .songname {
      font-size: 16rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      text-align: center;
    }
    .singername {
      margin-top: 5rem;
      color: #fff;
    }
  }
}
div.artist {
  background-color: #fcfcfd;
  .hd {
    width: 100%;
    position: relative;
    &::after {
      content: "";
      background-color: rgba(0, 0, 0, 0.18);
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      z-index: 0;
    }
    > img {
      width: 100%;
      display: block;
    }
    .content {
      position: absolute;
      left: 16rem;
      bottom: 30rem;
      z-index: 2;
      color: white;
      h2 {
        height: 20rem;
        line-height: 20rem;
        font-size: 20rem;
      }
      h4 {
        font-size: 13rem;
        margin-top: 15rem;
      }
      > div.nick {
        margin-top: 15rem;
        opacity: 0.8;
        font-size: 12rem;
        line-height: 12rem;
        > p:not(:first-child) {
          margin-top: 10rem;
        }
        .fs {
          font-weight: 500;
          > span {
            font-weight: 400;
          }
        }
        p {
          .skill {
            display: inline-block;
            opacity: 0.6;
            border-radius: 4rem;
            padding: 2rem;
            border: 1rem solid white;
            transform: scale(0.7);
            margin-left: -4px;
          }
        }
      }
    }
  }
  > h2 {
    font-size: 18rem;
    z-index: 2;
    margin-top: -5rem;
    padding: 15rem;
    background: #fff;
    font-weight: 700;
    &.h2Show {
      > span {
        color: #888;
        font-size: 12rem;
        font-weight: 400;
        margin-left: 10rem;
      }
    }
  }
  .intro {
    background-color: #fff;
    padding: 0 16rem 18rem;
    color: #333;
    > p.shou {
      color: #666;
      overflow: hidden;
      text-overflow: ellipsis;
      -webkit-line-clamp: 3;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-text-size-adjust: 100%;
      font-size: 13.5rem;
    }
    > p.zhang {
      color: #666;
      font-size: 14rem;
      line-height: 20rem;
    }
    > div.full {
      font-size: 14rem;
      color: #666;
      text-align: center;
      margin-top: 25rem;
      > i {
        display: inline-block;
        position: relative;
        width: 9rem;
        height: 9rem;
        background: url(data:image/png;base64,);
        background-size: contain;
        background-repeat: no-repeat;
        left: 4rem;
        top: 0;
      }
    }
  }
  .albumClass {
    display: flex;
    padding: 0 15rem 20rem;
    > figure {
      width: 80rem;
      height: 80rem;
      flex-shrink: 0;
      margin-right: 25rem;
      position: relative;
      &::after {
        position: absolute;
        content: "";
        width: 90px;
        height: 100%;
        background: url("https://s3.music.126.net/mobile-new/img/album@3x.png?0ec33f3%E2%80%A6");
        background-size: contain;
        background-repeat: no-repeat;
        right: -10px;
      }
      img {
        width: 80rem;
      }
    }
    > div.content {
      > h4 {
        font-size: 15rem;
      }
      > p:first-of-type {
        font-size: 13rem;
      }
      p {
        color: #888;
        &:last-child {
          margin-top: 10rem;
        }
      }
    }
  }

  .more-concert {
    padding-top: 23rem;
    padding-bottom: 23rem;
    background: #fff;
    font-size: 14rem;
    color: #666;
    text-align: center;
    > i {
      -webkit-transform: rotate(-90deg);
      -ms-transform: rotate(-90deg);
      transform: rotate(-90deg);
      width: 12rem;
      height: 12rem;
      top: 2rem;
      display: inline-block;
      position: relative;
      background: url("data:image/png;base64,");
      background-size: contain;
      background-repeat: no-repeat;
      left: 4rem;
    }
  }
  .artistFooter {
    position: fixed;
    bottom: -8rem;
    left: 0;
    width: 100%;
    background-color: white;
    border-top: 1px solid rgb(231, 231, 231);
    display: flex;
    justify-content: center;
    align-items: flex-end;
    height: 62rem;
    padding: 11rem 30rem;
    > a {
      display: inline-block;
      width: 100%;
      height: 40rem;
      background-color: #ff3a3a;
      color: white;
      font-size: 18rem;
      border-radius: 100vw;
      text-align: center;
      line-height: 40rem;
      text-decoration: none;
    }
  }
}
</style>
