import Vue from 'vue'
import './plugins/axios'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false
Vue.filters = {
  forNameFilter(s,n){
      console.log(s,n);
      let a = "";
      n.forEach((e, i) => {
        a += e.name + (i === n.length - 1 ? "" : " /");
      });
      return a;
  }
}
new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
