<template>
  <div class="seek-li">
    <div @click="$emit('click-li', value)" class="top">
      <span>搜索"{{ value }}"</span>
    </div>
    <ul v-show="isShow && isOK">
      <li v-for="(s, index) in seekArr" :key="index">
        <i></i>
        <div @click="$emit('click-li', s.keyword)">{{ s.keyword }}</div>
      </li>
    </ul>
    <div style="display: flex; justify-content: center" v-show="!isShow && !isOK">
      <img
        src="data:image/gif;base64,"
        alt=""
      />
    </div>
    <div style="text-align: center" v-show="isShow && !isOK">加载失败！</div>
  </div>
</template>

<script>
export default {
  props: {
    value: String,
    isResult: Boolean,
    focusValue: String,
  },
  data: () => ({
    seekArr: [],
    fandou: null,
    isShow: false,
    isOK: false,
  }),
  created() {
    if (this.value != "") {
      this.fandou = setTimeout(() => {
        console.log("create:" + this.value);
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest", {
            params: {
              keywords: this.value,
              type: "mobile",
            },
          })
          .then((res) => {
            console.log(res.data.result.allMatch);
            this.seekArr = res.data.result?.allMatch;
            this.isOK = true;
          })
          .catch(() => {
            this.isOK = false;
          })
          .finally(() => {
            this.isShow = true;
          });
      }, 300);
    }
  },
  watch: {
    value() {
      this.isShow = false;
      this.isOK = false
      clearTimeout(this.fandou);
      if (this.value != "" && !this.isResult) {
        this.fandou = setTimeout(() => {
          console.log(this.value);
          this.axios
            .get("https://apis.netstart.cn/music/search/suggest", {
              params: {
                keywords: this.value,
                type: "mobile",
              },
            })
            .then((res) => {
              console.log(res.data.result.allMatch);
              this.seekArr = res.data.result?.allMatch;
              this.isOK = true;
            })
            .catch(() => {
              this.isOK = false;
            })
            .finally(() => {
              this.isShow = true;
            });
        }, 300);
      }
    },
    focusValue() {
      this.isShow = false;
      clearTimeout(this.fandou);
      if (this.focusValue != "" && !this.isResult) {
        this.fandou = setTimeout(() => {
          console.log(this.value);
          this.axios
            .get("https://apis.netstart.cn/music/search/suggest", {
              params: {
                keywords: this.value,
                type: "mobile",
              },
            })
            .then((res) => {
              // console.log(res.data.result.allMatch);
              this.seekArr = res.data.result?.allMatch;
              this.isOK = true;
              this.$emit('focus-updata')
            })
            .catch(() => {
              this.isOK = false;
            })
            .finally(() => {
              this.isShow = true;
            });
        }, 300);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.seek-li {
  .top {
    height: 50rem;
    padding-right: 10rem;
    margin-left: 10rem;
    border-bottom: 1rem solid rgba(136, 136, 136, 0.192);
    display: flex;
    align-items: center;
    color: #507daf;
    font-size: 15rem;
  }
  > ul {
    > li {
      display: flex;
      align-items: center;
      padding: 10rem;
      height: 45rem;
      width: 100%;
      > i {
        flex-shrink: 0;
        width: 15rem;
        height: 15rem;
        margin-right: 7rem;
        display: inline-block;
        background-image: url("data:image/svg+xml;base64,");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
      }
      > div {
        width: 100%;
        font-size: 15rem;
        color: #333;
        border-bottom: 1rem solid rgba(136, 136, 136, 0.108);
        line-height: 45rem;
      }
    }
  }
}
</style>
