<template>
  <div class="playlist" v-if="album?.blurPicUrl">
    <div class="playing_title">
      <i
        class="fa fa-reply"
        @click="
          // $router.push({ path: beforePath });
          $router.back()
        "
      ></i>
      <i class="fa fa-share-square-o"></i>
    </div>
    <!-- 背景图 -->
    <div class="bg" :style="'backgroundImage:url(' + album.blurPicUrl + ')'"></div>

    <!-- 顶部 -->
    <header>
      <!-- 封面 -->
      <div class="cover">
        <i>专辑</i>
        <span class="album_bottom"></span>
        <img :src="album.picUrl+'?imageView=1&type=webp&thumbnail=200x0'" width="126" alt="" />
      </div>
      <!-- 描述 -->
      <div class="description">
        <p>{{ album.name }}</p>
        <div class="author">
          <p>
            歌手：<span>{{ album.artists[0].name }}</span>
          </p>
          <p>发行时间：{{ publishtime.split("/")[0] }}-{{ publishtime.split("/")[1].padStart(2, "0") }}-{{ publishtime.split("/")[2].padStart(2, "0") }}</p>
        </div>
      </div>
    </header>

    <div class="album-description" @click="toggleClass == 'shou' ? (toggleClass = 'open') : (toggleClass = 'shou')">
      简介：
      <p :class="toggleClass">{{ album.description }}</p>
      <span class="down" v-show="toggleClass == 'shou'"></span>
    </div>

    <!-- 详情列表 -->
    <!-- 单曲 -->
    <SongsDetails :playStatus="playStatus" :songId="songId" @playSong="$emit('playSong', $event)" @play-list="$emit('play-list', $event)" :singlesongs="singlesongs" :album="album">
    </SongsDetails>
    <!-- 评论 -->
    <CommentsDetails :hotcomments="hotcomments" :newcomments="newcomments" :total="total"> </CommentsDetails>
  </div>
</template>

<script>
import SongsDetails from "@/components/SongsDetails.vue";
import CommentsDetails from "@/components/CommentsDetails.vue";
export default {
  props: {
    playStatus: Boolean,
    songId: Number,
  },
  components: {
    SongsDetails,
    CommentsDetails,
  },
  data: function () {
    return {
      album: [],
      singlesongs: [],
      hotcomments: [],
      newcomments: [],
      total: null,
      toggleClass: "shou",
    };
  },
  computed: {
    publishtime() {
      return new Date(this.album.publishTime).toLocaleDateString();
    },
  },
  methods: {
    getAlbum: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/album", {
          params: { id },
        })
        .then((res) => {
          this.album = res.data.album;
          this.singlesongs = res.data.songs;
        });
    },
    getComments: function (id) {
      this.axios
        .get("https://apis.netstart.cn/music/comment/album", {
          params: { id, limit: 10 },
        })
        .then((res) => {
          this.hotcomments = res.data.hotComments;
          this.newcomments = res.data.comments;
          this.total = res.data.total;
        });
    },
  },

  created: function () {
    this.getAlbum(this.$route.query.id);
    this.getComments(this.$route.query.id);
  },
};
</script>

<style lang="less" scoped>
.playing_title {
  width: 100%;
  height: 45rem;
  padding: 0 30rem;
  position: fixed;
  top: 0;
  z-index: 9999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.115);
  .playing_song {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    .songname {
      font-size: 16rem;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      width: 100%;
      text-align: center;
    }
    .singername {
      margin-top: 5rem;
      color: #fff;
    }
  }
}
.playlist {
  overflow: hidden;
  .bg {
    width: 100vw;
    height: 100vh;
    position: absolute;
    z-index: -1;
    filter: blur(20rem);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center -200rem;
  }
  .album-description {
    padding: 10rem 20rem;
    font-size: 14rem;
    color: #666;
    background: #f8f8f8;
    position: relative;
    p {
      margin-top: 14rem;
      margin-bottom: 10rem;
      width: 85vw;
      &.shou {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      &.open {
        white-space: pre-wrap;
      }
    }

    span {
      background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAANCAQAAAAZZWZbAAAAkElEQVR4AY2RtQEDMQxFX+D4RsgA6cN4XGr/cQJyEzD9yvCeZIAVWxbEJKNhDQ3CiUUE3iE0UNAjnFniS674nVQnqlw8iiHuJN/+hQRbzBluJP8tryQWfLAXy2j/61Aa3H7clOb7pJSMFtyipABUip/9T55yR2hIqZnifihRpVX8aMGtyg1BODAnMglHdnb8CYQbEwGgBxiKAAAAAElFTkSuQmCC");
      position: absolute;
      bottom: 3px;
      right: 10rem;
      z-index: 3;
      width: 15px;
      height: 15px;
      background-position: 50%;
      background-repeat: no-repeat;
      background-size: 12px auto;
    }
  }
  header {
    padding: 50rem 15rem 30rem ;
    display: flex;
    .cover {
      position: relative;
      i {
        display: block;
        position: absolute;
        top: 12rem;
        left: 0;
        background: rgba(217, 48, 48, 0.8);
        border-top-right-radius: 10rem;
        border-bottom-right-radius: 10rem;
        color: white;
        font-size: 12rem;
        padding: 1rem 10rem;
      }
      span.album_bottom {
        position: absolute;
        left: 0;
        top: 0;
        z-index: 1;
        height: 100%;
        padding-left: 115%;
        background: url(//s3.music.126.net/mobile-new/img/album@2x.png?4d5d63f…=) 100% no-repeat;
        background-size: contain;
      }
    }
    .description {
      margin-left: 30rem;
      font-size: 17rem;
      padding-top: 5rem;
      color: white;
      .author {
        margin-top: 30rem;
        font-size: 12rem;
        position: relative;
        img {
          border-radius: 50rem;
          margin-right: 5rem;
        }
        i.type {
          display: block;
          position: absolute;
          bottom: 0;
          left: 23rem;
          width: 12rem;
          height: 12rem;
          background-image: url("../assets/usericn_2x.png");
          background-position: -40rem 0;
          background-size: 75px 25px;
          background-repeat: no-repeat;
        }
        p {
          color: hsla(0, 0%, 100%, 0.7);
          font-size: 14rem;
          margin-bottom: 8rem;
          &:nth-child(2) {
            font-size: 12rem;
          }
        }
        span {
          color: white;
        }
      }
    }
  }
}
</style>
