<template>
  <div class="seek-result">
    <div>
      <span v-if="orders.length">最佳匹配</span>
      <ul v-if="orders.length">
        <li  v-for="(o, i) in orders" :key="i" v-show="o == 'artist' || o == 'album' || o == 'playlist'">
          <figure v-if="o != 'new_mlog'">
            <img :src="album[o][0][ablumIndex[o]?.img]+'?imageView=1&type=webp&thumbnail=60x0'" />
          </figure>
          <div class="artist" @click="$router.push({ path: '/artist', query: { id: album[o][0].id, albumId: album['album'] ? album['album'][0].id : '' } })">
            <h4 ref="h4" v-if="o != 'new_mlog' && o == 'artist'">
              {{ album[o][0][ablumIndex[o]?.title] }}: {{ album[o][0][ablumIndex[o]?.name] }}
              {{ album[o][0][ablumIndex[o]?.fName][0] ? "(" + album[o][0][ablumIndex[o]?.fName][0] + ")" : "" }}
            </h4>
          </div>

          <div v-if="o != 'new_mlog' && o == 'album'" class="album" @click="$router.push({ path: '/album', query: { id: album[o][0].id } })">
            <h4 ref="h4">{{ album[o][0][ablumIndex[o]?.title] }}: {{ album[o][0][ablumIndex[o]?.name] }}</h4>
            <p ref="p">{{ album[o][0][ablumIndex[o]?.fName][0]?.name }}</p>
          </div>

          <div @click="$router.push({ path: '/playlist', query: { id: album[o][0].id } })" class="playlist" v-if="o != 'new_mlog' && o == 'playlist'">
            <h4 ref="h4">{{ ablumIndex[o].title }}: {{ album[o][0].name }}</h4>
            <p ref="p">by {{ album[o][0][ablumIndex[o]?.fName].nickname }}</p>
          </div>
          <i></i>
        </li>
      </ul>
    </div>
    <ul v-if="songResult">
      <seekResultSongLi :playStatus="playStatus" :songId="songId" @playSong="$emit('playSong',$event);$emit('play-list',songResult)" :value="seekValue" v-for="s in songResult" :key="s?.id" :n="s"></seekResultSongLi>
    </ul>
  </div>
</template>

<script>
import seekResultSongLi from "./seekResultSongLi.vue";
export default {
  props: {
    seekValue: String,
    songId:Number,
    playStatus:Boolean
  },
  data: () => ({
    album: [],
    songResult: [],
    orders: [],
    ablumIndex: {
      artist: {
        name: "name",
        title: "occupation",
        img: "img1v1Url",
        fName: "alias",
      },
      album: {
        name: "name",
        title: "type",
        img: "picUrl",
        fName: "artists",
      },
      playlist: {
        name: "name",
        title: "歌单",
        img: "coverImgUrl",
        fName: "creator",
      },
    },
    n: null,
  }),
  created() {
    let s = this.axios("https://apis.netstart.cn/music/cloudsearch", {
      params: {
        keywords: this.seekValue,
        limit: 30,
        offset: 0,
      },
    });

    let z = this.axios("https://apis.netstart.cn/music/search/multimatch", {
      params: {
        keywords: this.seekValue,
      },
    });
    Promise.all([z, s]).then(([z, s]) => {
      this.album = z.data.result;
      this.songResult = s.data.result.songs;
      this.orders = z.data.result.orders;
    });
  },
  components: {
    seekResultSongLi,
  },
  updated() {
    let a = this.$refs.h4;
    let b = this.$refs.p;
    const highlight = (arr) => {
      arr?.forEach((el, i) => {
        // console.log(this.seekValue, " ", el.innerHTML);
        let c = el.innerHTML.match(new RegExp(this.seekValue, "img"));
        let aa = "";
        let ai = 0;
        c?.forEach((e) => {
          let ab = el.innerHTML.slice(ai);
          let c = ab.indexOf(e);
          aa += el.innerHTML.slice(ai, c + ai);
          aa += `<span style="color:#507daf">${e}</span>`;
          ai += c + e.length;
        });
        aa += el.innerHTML.slice(ai);
        arr[i].innerHTML = aa == "" ? el.innerHTML : aa;
      });
    };
    highlight(a);
    highlight(b);

    // a.forEach((el, i) => {
    //   console.log(this.seekValue, el.innerHTML);
    //   let c = el.innerHTML.match(new RegExp(this.seekValue, "img"));
    //   let aa = "";
    //   let ai = 0;
    //   c?.forEach((e) => {
    //     let ab = el.innerHTML.slice(ai);
    //     let c = ab.indexOf(e);
    //     aa += el.innerHTML.slice(ai, c);
    //     aa += `<span style="color:#507daf">${e}</span>`;
    //     ai = c + e.length;
    //   });
    //   a[i].innerHTML = aa == "" ? el.innerHTML : aa;
    // });

    // b.forEach((el, i) => {
    //   console.log(this.seekValue, el.innerHTML);
    //   let c = el.innerHTML.match(new RegExp(this.seekValue, "img"));
    //   let aa = "";
    //   let ai = 0;
    //   c?.forEach((e) => {
    //     let ab = el.innerHTML.slice(ai);
    //     let c = ab.indexOf(e);
    //     aa += el.innerHTML.slice(ai, c);
    //     aa += `<span style="color:#507daf">${e}</span>`;
    //     ai = c + e.length;
    //   });
    //   b[i].innerHTML = aa == "" ? el.innerHTML : aa;
    // });
  },
};
</script>

<style lang="less" scoped>
.seek-result {
  padding: 8rem 0 4rem 0;
  > div:nth-of-type(1) {
    > span {
      padding-left: 10rem;
      color: #888;
    }
    > ul {
      padding: 0 10rem;
      > li {
        display: flex;
        align-items: center;
        height: 50rem;
        padding: 8rem 10rem 8rem 0;
        box-sizing: content-box;
        figure {
          flex-shrink: 0;
          width: 50rem;
          height: 50rem;
          margin-right: 15rem;
          > img {
            width: 100%;
          }
        }
        h4 {
          width: 260rem;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
        }
        > i {
          flex-shrink: 0;
          display: inline-block;
          background-image: url("data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHZpZXdCb3g9IjAgMCAxNiAyNiI+PHBhdGggZmlsbD0ibm9uZSIgc3Ryb2tlPSIjOWI5YjliIiBzdHJva2Utd2lkdGg9IjMiIHN0cm9rZS1saW5lY2FwPSJzcXVhcmUiIHN0cm9rZS1taXRlcmxpbWl0PSIxMCIgZD0iTTIuOTM5IDIzLjkzOUwxMy44NzkgMTMgMi45MzkgMi4wNjEiLz48L3N2Zz4=");
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
          width: 8rem;
          height: 13rem;
          margin: 0 8rem 0 10rem;
        }
        .artist {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
        }
        .album {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
          > p {
            height: 16rem;
            color: #999;
            line-height: 16rem;
            font-size: 12rem;
          }
        }
        .playlist {
          > h4 {
            height: 30rem;
            line-height: 30rem;
            font-size: 17rem;
          }
          > p {
            height: 16rem;
            color: #999;
            line-height: 16rem;
            font-size: 12rem;
          }
        }
      }
    }
  }
}
</style>
