<template>
  <div class="lyric">
    <div class="loadingImg" v-if="loading">
      <!-- ../assets/playing.gif -->
      <img src="" style="margin: auto; display: block" alt="" />
    </div>

    <!-- 背景 -->
    <div class="bg"></div>

    <!-- 头部 -->
    <header class="playing_title">
      <i
        class="fa fa-reply"
        @click="
          // $router.push({ path: beforePath });
          $router.back()
        "
      ></i>
      <div class="playing_song">
        <p class="songname">{{ song?.name }}</p>
        <!-- <p class="singername">{{ song?.ar[0].name }}</p> -->
      </div>
      <i class="fa fa-share-square-o"></i>
    </header>

    <!-- 黑胶唱片、歌词 -->
    <section class="playing_main">
      <PlayLyric v-if="showlyric" @click.native="showlyric = false" :lyricID="songId" :currentTime="currentTime"></PlayLyric>
      <PlayDics v-else :songDetails="song" :playing="playing" @click.native="showlyric = true"></PlayDics>
    </section>


    <!-- 底部 -->
    <footer class="playing_features">
      <!-- 进度条 -->
      <section class="features_progressBar">
        <span v-show="$refs?.input?.value">{{
          Math.floor($refs?.input?.value / 60)
            .toString()
            .padStart(2, "0") +
          ":" +
          ($refs?.input?.value % 60).toFixed(0).toString().padStart(2, "0")
        }}</span>
        <span v-show="!$refs?.input?.value">00:00</span>
        <label>
          <div class="Progressbar">
            <div class="time" :style="`width:${jindu}%`">
              <i class="point"></i>
            </div>
          </div>
          <!-- step="0.01" -->
          <input
            type="range"
            ref="input"
            min="0"
            :max="duration"
            :value="draging ? $refs.input.value : currentTime"
            @input="
              draging = true;
              updateInput();
              updateInputValue = false;
            "
            @change="
              $emit('update-time', $refs.input.value);
              draging = false;
            "
          />
        </label>
        <span v-show="$refs?.input?.max">{{
          Math.floor($refs?.input?.max / 60)
            .toString()
            .padStart(2, "0") +
          ":" +
          ($refs?.input?.max % 60).toFixed(0).toString().padStart(2, "0")
        }}</span>
        <span v-show="!$refs?.input?.max">00:00</span>
      </section>

      <!-- 功能键 -->
      <section class="features_btns">
        <button class="play-model" @click="$emit('change-model')" ref="svgButton">
          <svg
            v-if="patternIndex == 'random'"
            t="1664299617020"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="7053"
            :width="$refs.svgButton?.offsetHeight"
            :height="$refs.svgButton?.offsetHeight"
          >
            <path
              d="M914.2 705L796.4 596.8c-8.7-8-22.7-1.8-22.7 10V688c-69.5-1.8-134-39.7-169.3-99.8l-45.1-77 47-80.2c34.9-59.6 98.6-97.4 167.4-99.8v60.1c0 11.8 14 17.9 22.7 10l117.8-108.1c5.8-5.4 5.8-14.6 0-19.9L796.4 165c-8.7-8-22.7-1.8-22.7 10v76H758c-4.7 0-9.3 0.8-13.5 2.3-36.5 4.7-72 16.6-104.1 35-42.6 24.4-78.3 59.8-103.1 102.2L513 432l-24.3-41.5c-24.8-42.4-60.5-77.7-103.1-102.2C343 263.9 294.5 251 245.3 251H105c-22.1 0-40 17.9-40 40s17.9 40 40 40h140.3c71.4 0 138.3 38.3 174.4 99.9l47 80.2-45.1 77c-36.2 61.7-103 99.9-174.4 99.9H105c-22.1 0-40 17.9-40 40s17.9 40 40 40l142 0.1h0.2c49.1 0 97.6-12.9 140.2-37.3 42.7-24.4 78.3-59.8 103.2-102.2l22.4-38.3 22.4 38.3c24.8 42.4 60.5 77.8 103.2 102.2 33.1 18.9 69.6 30.9 107.3 35.4 3.8 1.2 7.8 1.8 11.9 1.8l15.9 0.1v55c0 11.8 14 17.9 22.7 10L914.2 725c5.9-5.5 5.9-14.7 0-20z"
              p-id="7054"
              fill="#ffffff"
            ></path>
          </svg>
          <svg
            v-if="patternIndex == 'order'"
            t="1664300595329"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="7854"
            :width="$refs.svgButton?.offsetHeight"
            :height="$refs.svgButton?.offsetHeight"
          >
            <path d="M941 414.6H83c-11 0-20-9-20-20s9-20 20-20h858c11 0 20 9 20 20s-8.9 20-20 20z" p-id="7855" fill="#ffffff"></path>
            <path
              d="M937.2 405.4c-5.1 0-10.2-2-14.1-5.9L780.7 257.1c-7.8-7.8-7.8-20.5 0-28.3 7.8-7.8 20.5-7.8 28.3 0l142.4 142.4c7.8 7.8 7.8 20.5 0 28.3-4 3.9-9.1 5.9-14.2 5.9zM941 649.4H83c-11 0-20-9-20-20s9-20 20-20h858c11 0 20 9 20 20s-8.9 20-20 20z"
              p-id="7856"
              fill="#ffffff"
            ></path>
            <path
              d="M794.8 801c-5.1 0-10.2-2-14.1-5.9-7.8-7.8-7.8-20.5 0-28.3L923 624.5c7.8-7.8 20.5-7.8 28.3 0s7.8 20.5 0 28.3L808.9 795.1c-3.9 3.9-9 5.9-14.1 5.9z"
              p-id="7857"
              fill="#ffffff"
            ></path>
          </svg>
          <svg
            v-if="patternIndex == 'cycle'"
            t="1664300499949"
            class="icon"
            viewBox="0 0 1024 1024"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            p-id="7662"
            :width="$refs.svgButton?.offsetHeight"
            :height="$refs.svgButton?.offsetHeight"
          >
            <path
              d="M64.532459 511.40739a63.893258 63.893258 0 0 0 63.893259-63.893258V383.620873a63.893258 63.893258 0 0 1 63.893258-63.893258h612.736347l-83.061236 82.422303a63.893258 63.893258 0 0 0 0 90.728427 63.893258 63.893258 0 0 0 90.728427 0l191.679775-191.679775a63.893258 63.893258 0 0 0 0-90.728427l-191.679775-191.679775a63.893258 63.893258 0 0 0-90.728427 90.728427L805.055323 191.941098H192.318976a191.679775 191.679775 0 0 0-191.679775 191.679775v63.893259a63.893258 63.893258 0 0 0 63.893258 63.893258z"
              fill="#ffffff"
              p-id="7663"
            ></path>
            <path
              d="M959.038076 511.40739a63.893258 63.893258 0 0 0-63.893258 63.893258v63.893259a63.893258 63.893258 0 0 1-63.893259 63.893258H218.515212l83.061236-81.783371a63.893258 63.893258 0 0 0 0-90.089494 63.893258 63.893258 0 0 0-90.728427 0l-191.679775 191.679775a63.893258 63.893258 0 0 0 0 90.728427l191.679775 191.679775a63.893258 63.893258 0 0 0 90.728427-91.36736L218.515212 830.873682H831.251559a191.679775 191.679775 0 0 0 191.679775-191.679775V575.300648a63.893258 63.893258 0 0 0-63.893258-63.893258z"
              fill="#ffffff"
              p-id="7664"
            ></path>
          </svg>
        </button>
        <button @click="$emit('playPattern', 'prev')">
          <i class="fa fa-step-backward"></i>
        </button>
        <button v-if="!playing" @click.stop="$emit('playStatus')">
          <i class="fa fa-play"></i>
        </button>
        <button v-else @click.stop="$emit('pauseStatus')">
          <i class="fa fa-pause"></i>
        </button>
        <button @click="$emit('playPattern', 'next')">
          <i class="fa fa-step-forward"></i>
        </button>
        <button @click="$emit('play-list-show')">
          <i class="fa fa-bars"></i>
        </button>
      </section>
    </footer>
  </div>
</template>

<script>
import PlayDics from "@/components/PlayDics.vue";
import PlayLyric from "@/components/PlayLyric.vue";

export default {
  components: {
    PlayDics,
    PlayLyric,
  },
  props: {
    playing: Boolean,
    currentTime: Number,
    duration: Number,
    model: String,
    show: Boolean,
    songId: [Number, String],
    showPlayList: Boolean,
    song: Object,
    patternIndex: String,
  },
  computed: {
    jindu: function () {
      if (this.draging || !this.updateInputValue) {
        return (this.inputValue / this.duration) * 100;
      } else {
        return (this.currentTime / this.duration) * 100;
      }
    },
  },
  data() {
    return {
      // songDetails: null,
      showlyric: false,
      tmpCurrentTime: this.currentTime,
      draging: false,
      lyricID: null,
      loading: false,
      inputValue: this.currentTime,
      updateInputValue: true,
      beforePath:'/'
    };
  },
  beforeRouteEnter(to,from,next){
    // console.log(to);
    // console.log(from);
    next()
  },
  watch: {
    currentTime: function (newTime) {
      if (!this.draging) {
        this.updateInputValue = true;
        this.tmpCurrentTime = newTime;
      }
    },
    songId: function (n) {
      this.getSongDetails(n);
    },
  },
  methods: {
    // 获取歌曲详情
    getSongDetails(ids) {
      this.axios
        .get("https://apis.netstart.cn/music/song/detail", {
          params: {
            ids,
          },
        })
        .then((res) => {
          this.songDetails = res.data.songs[0];
        });
    },
    updateInput() {
      this.inputValue = this.$refs.input.value;
    },
  },
};
</script>

<style lang="less" scoped>
.custom-classes-transition {
  transition: all 3s;
}
.lyric {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  color: white;
  .loadingImg {
    width: 100%;
    height: 100vh;
    position: absolute;
    z-index: 111;
    background: white;
    flex-shrink: 1;
    img {
      position: absolute;
      top: 35%;
      left: 38%;
      width: 100rem;
      height: 100rem;
    }
  }
  i {
    color: white;
    font-size: 18rem;
  }
  .bg {
    width: 100%;
    height: 100%;
    position: absolute;
    background: #1d1d1d;
    z-index: -1;
  }
  .playing_title {
    width: 100%;
    height: 80rem;
    padding: 30rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .playing_song {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 70%;
      .songname {
        font-size: 16rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
        text-align: center;
      }
      .singername {
        margin-top: 5rem;
        color: hsla(0, 0%, 100%, 0.6);
      }
    }
  }
  .playing_main {
    overflow: hidden;
    width: 100%;
    flex-grow: 1;
  }
  .playing_features {
    height: 25%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 30rem;
    .features_progressBar {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;
      height: 30%;
      span {
        color: hsla(0, 0%, 100%, 0.6);
        display: block;
        width: 10%;
      }
      label {
        width: 75%;
        height: 3px;
        position: relative;
        display: block;
        border-radius: 30px;
        // overflow: hidden;
        .Progressbar {
          width: 100%;
          height: 100%;
          background: hsla(0, 0%, 100%, 0.6);
          border-radius: 30px;
          position: absolute;
          z-index: -1;
          .time {
            width: 0%;
            height: 100%;
            border-radius: 30px;
            background: white;
            position: relative;
            i.point {
              position: absolute;
              top: -3rem;
              right: -4rem;
              display: block;
              width: 8rem;
              height: 8rem;
              background: white;
              border-radius: 50%;
            }
          }
        }

        input {
          width: calc(100% + 15px);
          position: absolute;
          top: -7px;
          left: -8px;
          display: block;
          opacity: 0;
        }
      }
    }
    .features_btns {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      height: 50%;
      button {
        background: none;
        width: 40rem;
        height: 40rem;
        border-radius: 50%;
        border: 0;
        font-size: 20rem;
        &:nth-child(3) {
          border: 1rem solid white;
          outline: 0;
          width: 50rem;
          height: 50rem;
        }
        &.play-model {
          background: transparent;
          // background-image: url("../assets/sequence.png");
          background-repeat: no-repeat;
          background-size: 20rem;
          background-position: 11px 11px;
          display: flex;
          align-items: center;
          justify-content: center;
          > svg {
            width: 20rem;
          }
        }
      }
    }
  }
}
</style>
