<template>
  <div id="app">
    <!-- 视图 -->
    <router-view
      @playSong="
        song = $event[1];
        songId = $event[0];
      "
      :song="song"
      :playStatus="playStatus"
      :playing="playStatus"
      :songId="songId"
      :currentTime="currentTime"
      @play-list="playListFn($event)"
      :duration="duration"
      :selectSongs="playList"
      @playStatus="$refs.audio.play()"
      @pauseStatus="$refs.audio.pause()"
      @playPattern="playPattern($event)"
      @play-list-show="playListShow = !playListShow"
      :patternIndex="patternIndex"
      @change-model="patternIndex = pattern.findIndex((e) => e == patternIndex) == pattern.length - 1 ? pattern[0] : pattern[pattern.findIndex((e) => e == patternIndex) + 1]"
      @update-time="$refs.audio.currentTime = $event"
    />
    <!-- 播放器标签 -->
    <audio
      @pause="playStatus = false"
      @play="playStatus = true"
      ref="audio"
      v-if="song?.id"
      :src="`https://music.163.com/song/media/outer/url?id=${songId}.mp3`"
      style="position: fixed; top: 180rem; left: 0"
      autoplay
      @timeupdate="log"
      @ended="playPattern('ended')"
    ></audio>
    <!-- controls   -->
    <!-- 播放歌曲的低部通栏 -->
    <PlaySongBar
      class="play-song-bar"
      @click.native="$router.push({ path: '/lyric', query: { id: songId } })"
      v-if="song"
      v-show="$route.path != '/lyric'"
      @playStatus="$refs.audio.play()"
      @pauseStatus="$refs.audio.pause()"
      :playStatus="playStatus"
      :currentTime="currentTime"
      :duration="$refs.audio?.duration"
      :song="song"
      :songId="songId"
      @play-list-show="playListShow = !playListShow"
    ></PlaySongBar>
    <!-- 歌曲列表 -->
    <SongListView
      @playSong="
        song = $event;
        songId = $event.id;
      "
      @click.native="playListShow = false"
      :playStatus="playStatus"
      v-show="playListShow"
      :playList="playList"
      class="songList"
      :song="song"
    ></SongListView>
    <div v-if="song && $route.path !== '/lyric'" style="height: 60rem"></div>
  </div>
</template>

<script>
import PlaySongBar from "@/components/playSongBar.vue";
import SongListView from "@/views/songListView.vue";
export default {
  components: {
    PlaySongBar,
    SongListView,
  },
  data: function () {
    return {
      song: localStorage.getItem("song") ? JSON.parse(localStorage.getItem("song")) : null,
      songId: 0,
      playStatus: false,
      currentTime: 0,
      playList: localStorage.getItem("playList") ? JSON.parse(localStorage.getItem("playList")) : null,
      playListShow: false,
      pattern: ["order", "random", "cycle"],
      patternIndex: "order",
      a: 1,
      b: 2,
      duration: 0,
    };
  },
  created: function () {
    if (this.song) {
      this.songId = this.song.id;
    }
    // console.log(this.a);
  },
  mounted() {
    // console.log(this.b);
  },
  methods: {
    playPattern(ended = "next") {
      switch (this.patternIndex) {
        case "order":
          if (ended == "prev") {
            var aa = this.playList.findIndex((e) => e.id == this.songId) - 1;
            this.song = aa < 0 ? this.playList[this.playList.length - 1] : this.playList[aa];
          } else {
            var a = this.playList.findIndex((e) => e.id == this.songId) + 1;
            console.log(a);
            this.song = a === this.playList.length ? this.playList[0] : this.playList[a];
          }
          break;
        case "random":
          var b = this.playList.slice().filter((e) => e.id !== this.songId);
          var c = Math.floor(Math.random() * b.length);
          this.song = b[c];
          break;
        case "cycle":
          if (ended == "ended") {
            this.$refs.audio.currentTime = 0;
            this.$refs.audio.play();
          } else {
            if (ended == "prev") {
              var aa2 = this.playList.findIndex((e) => e.id == this.songId) - 1;
              this.song = aa2 < 0 ? this.playList[this.playList.length - 1] : this.playList[aa2];
            } else {
              var a2 = this.playList.findIndex((e) => e.id == this.songId) + 1;
              this.song = a2 === this.playList.length ? this.playList[0] : this.playList[a2];
            }
          }
          break;
      }
      this.songId = this.song.id;
    },
    xxx() {
      this.playStatus = !this.playStatus;
      console.log(this.$refs.audio.paused);
    },
    log() {
      // console.log(this.$refs.audio.currentTime);
      this.currentTime = this.$refs.audio.currentTime;
      this.duration = this.$refs.audio.duration;
    },
    playListFn(p) {
      // console.log(p.slice());
      this.playList = p.slice();
    },
  },
  computed: {},
  watch: {
    playList(n) {
      // console.log(n, 123);
      localStorage.setItem("playList", JSON.stringify(n));
    },
    song(n) {
      localStorage.setItem("song", JSON.stringify(n));
    },
  },
};
</script>

<style lang="less" scoped>

#app {
  .play-song-bar{
    z-index: 10000;
  }
  .songList {
    position: fixed;
    z-index: 1000;
    bottom: 70rem;
    width: 100%;
    height: 60vh;
    // background-color: lightblue;
    padding: 10rem;
    &::before {
      content: "";
      width: 100vw;
      height: 100vh;
      background-color: rgba(0, 0, 0, 0.195);
      position: fixed;
      left: 0;
      top: 0;
      z-index: -1;
    }
  }
}
</style>
