<template>
  <div class="playList">
    <ul class="playListContent" v-if="playList?.length">
      <li class="content" v-for="p in playList" :key="p.id" @click.stop="$emit('playSong',p)"  :class="{ active: song?.id === p.id }">
        <h4>
          <div v-if="song?.id === p.id" :class="{activeLoding:true,play:playStatus}" >
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
          {{ p.name }} - {{ forName(p.song ? p.song.artists : p.ar) }}
        </h4>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    playList: Array,
    song: Object,
    playStatus:Boolean
  },
  data: () => ({}),
  created() {},
  methods: {
    forName(n) {
      let a = "";
      n.forEach((e, i) => {
        a += e.name + (i === n.length - 1 ? "" : " /");
      });
      return a;
    },
  },
};
</script>

<style lang="less" scoped>
@keyframes loding {
  from {
    height: 10rem;
  }
  to {
    height: 4rem;
  }
}
.playList {
  .playListContent {
    width: 100%;
    background-color: #fbfbfb;
    height: 100%;
    border-radius: 15rem;
    overflow-y: auto;
    overflow-x: hidden;
    box-shadow: 0 0 2rem 0 rgba(51, 51, 51, 0.427);
    &::-webkit-scrollbar {
      display: none;
      width: 0;
      height: 0;
    }
    li.content {
      width: 100%;
      flex-shrink: 1;
      line-height: 32rem;
      outline: 1rem solid rgba(162, 162, 162, 0.096);
      padding: 10rem 8rem;
      &.active {
        h4 {
          display: flex;
          align-items: center;
          color: red;
          > div.activeLoding {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            width: 10rem;
            margin:0 5rem;
            height: 10rem;
            > span {
              width: 2rem;
              height: 10rem;
              background-color: red;
              animation: loding 0.6s infinite ease-in-out alternate;
              animation-play-state: paused;
            }
            &.play {
              span {
                animation-play-state: running;
              }
            }
            > span:nth-of-type(1) {
              animation-delay: 0;
            }
            > span:nth-of-type(2) {
              animation-delay: -0.2s;
            }
            > span:nth-of-type(3) {
              animation-delay: -0.4s;
            }
            > span:nth-of-type(4) {
              animation-delay: -0.6s;
            }
          }
        }
      }
      h4 {
        color: #333;
        padding: 0 10rem;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        width: 100%;
        font-size: 14rem;
      }
    }
  }
}
</style>
